<template>
  <div :class="{'account-address-box': isPc,'account-address-box-mobile': !isPc}">
    <ul class="all-tips">
      <li class="row-tips">auVPN 不使用密碼登入。</li>
      <li class="row-tips">登入鏈接為登入唯一途徑，請妥善保存。</li>
      <li class="row-tips">可用帳戶郵箱隨時取回登入鏈接。</li>
      <li class="row-tips">匿名帳戶沒留郵箱，丟失私密鏈接後無法取回。</li>
    </ul>
    <div class="content-title" v-if="false">{{userAddress.length > 0 ? $t('AccountAddress.Address-Login-Save') : $t('AccountAddress.Click-Create-Anonymous')}}</div>
    <div class="content-left-box">
      <a-button class="btn-create-account" type="primary" v-if="userAddress.length === 0" @click.stop="createAccount">{{$t('AccountAddress.Create-Account')}}</a-button>
      <div style="display:flex;flex-direction: column;width: 100%;" v-if="userAddress.length > 0">
        <div style="position: relative;display:flex;width: 100%;">
          <span class="span-address">{{userAddress}}</span>
          <a-tooltip placement="top" trigger="click" :mouseLeaveDelay=1 :destroyTooltipOnHide=true
                     :visible="!hideCopyTips">
            <template slot="title">
              <span>{{$t('CreateAccount.Copied')}}</span>
            </template>
            <span class="copy-tips" @click.stop="copyAccountInfo" @mouseleave="hideCopyTips=true">{{$t('CreateAccount.Copy-Account-Address')}}</span>
          </a-tooltip>
        </div>
        <a-divider style="background-color: #DEE2E8;margin: 0px;"/>
        <div class="download-favorite-box">
          <div class="item-box" @click.stop="downloadAuAccountInfo">
            <span class="download-span" v-if="isPc">{{$t('AccountAddress.Download-Account-Info')}}</span>
            <a-tooltip placement="top" trigger="click" :mouseLeaveDelay=0.5 :destroyTooltipOnHide=true>
              <template slot="title">
                <span>{{$t('AccountAddress.Copied')}}</span>
              </template>
              <span class="download-span" v-if="!isPc" @click.stop="downloadAuAccountInfo()">{{$t('AccountAddress.Copy-Account-Info')}}</span>
            </a-tooltip>
          </div>
          <a-divider class="divider" type="vertical"/>
          <div class="item-box item-bookmark" v-if="isPc">
            <span class="download-span favorite-span" style="color: #3963EF;">{{$t('AccountAddress.Draw-To-Bookmark')}}</span>
            <a :href="userAddress" style="position: absolute;opacity: 0.1" @click.prevent="preventOpenAUAdress">{{$t('AccountAddress.Au-Account')}}</a>
            <!--<span class="bookmark-tips">將此按鈕拖到您的書籤欄</span>-->
            <!--<span class="login-tips-span" v-if="showBottomTips">{{$t('AccountAddress.Login-With-New-Package')}}</span>-->
          </div>
          <div class="item-box" v-if="!isPc" @click.stop="addToDeviceDesktop()">
            <span class="download-span favorite-span" style="color: #3963EF;">{{$t('AccountAddress.Add-Mobile-Desktop')}}</span>
          </div>
        </div>
      </div>
    </div>
    <!--<span class="login-tips-span" v-if="showBottomTips && !isPc">{{$t('AccountAddress.Login-With-New-Package')}}</span>-->
  </div>
</template>

<script>
  import {AU_HMAC, AU_SELECTED_SERVICE, AU_UUID} from "../requestServer";

  export default {
    name: 'AccountAddress',
    data() {
      return {
        isPc : this.$isPc(),
        userAddress: '',
        hideCopyTips: true,
        showBottomTips: !this.$route.path.endsWith('order/pay') && localStorage.getItem(AU_UUID) && localStorage.getItem(AU_HMAC) && localStorage.getItem(AU_SELECTED_SERVICE)
      };
    },
    props: {
      userAccount: {},
    },
    watch: {
      userAccount: {
        immediate: true,
        handler(value) {
          if (value !== undefined) {
            this.userAddress = value.address;
            //this.showBottomTips = !this.$route.path.endsWith('order/pay') && localStorage.getItem(AU_UUID) && localStorage.getItem(AU_HMAC) && localStorage.getItem(AU_SELECTED_SERVICE);
          }
        }
      },
    },
    mounted(){
      if (!this.$route.path.endsWith('order/pay') && localStorage.getItem(AU_UUID) && localStorage.getItem(AU_HMAC)) {
        this.userAddress = `https://www.ausvc.link?uuid=${localStorage.getItem(AU_UUID)}&hmac=${localStorage.getItem(AU_HMAC)}`;
      }
    },
    methods: {
      preventOpenAUAdress() {

      },
      copyAccountInfo() {
        var textarea = document.createElement("textarea");
        textarea.value = this.userAddress;
        textarea.readOnly = true;
        document.body.appendChild(textarea);
        textarea.select(); // 选择对象
        document.execCommand("Copy"); // 执行浏览器复制命令
        textarea.className = "textarea";
        textarea.style.display = "none";
        const that = this
        setTimeout(function () {
          // Open the channel
          that.hideCopyTips = true
        }, 1500);
        this.hideCopyTips = false
      },
      downloadAuAccountInfo(){
        if (this.isPc) {
          const element = document.createElement('a');
          element.setAttribute('href', 'data:text/plain;charset=utf-8,' + encodeURIComponent(`· ${this.$t('AccountAddress.Address-Login-Save')}\n· ${this.userAddress}\n` +
                  `· auVPN 不使用密碼登入。\n· 私密鏈接為登入唯一途徑，請妥善保存。\n· 可用帳戶郵箱隨時取回登入鏈接。\n· 匿名帳戶沒留郵箱，丟失登入鏈接後無法取回。`));
          element.setAttribute('download', `auVPN 帳戶登入鏈接（請妥善保存）`);
          element.style.display = 'none';
          element.click()
        } else {
          var textarea = document.createElement("textarea");
          textarea.value = this.userAddress;
          textarea.readOnly = true;
          document.body.appendChild(textarea);
          textarea.select(); // 选择对象
          document.execCommand("Copy"); // 执行浏览器复制命令
          textarea.className = "textarea";
          textarea.style.display = "none";
        }
      },
      addToDeviceDesktop() {

      },
      createAccount() {
        let param = {
          "recaptchaToken":"333333"
        };
        let that = this;
        this.$request.auCreatedNewAddress(param,function (data) {
          if (data && (data.status === 200 || data.status === 201) && data.data) {
            localStorage.setItem(AU_UUID, data.data.uuid);
            localStorage.setItem(AU_HMAC, data.data.hmac);
            that.userAddress = data.data.userAddress;
          }
        });
      }
    }
  };
</script>

