          background: #FFFFFF;
        }
        .common-box {
          border-radius: 8px;
          align-items: center;
          justify-content: center;
          display: flex;
          width: 100%;
        }
        .loading-box {
          border-top: 3px solid #3963EF;
        }
        .no-account-success-box {
          flex-direction: column;
          border-top: 3px solid #3963EF;
          .success-icon {
            margin-top: 40px;
            margin-bottom: 28px;
            bottom: 192px;
            display: inline-block;
            width: 48px;
            height: 48px;
            background: url('../assets/pay/success.svg') center no-repeat;
            background-size: contain;
          }
          .success-title {
            margin-bottom: 12px;
            font-size: 32px;
            font-weight: 500;
            line-height: 33px;
            color: #383E44;
          }
          .success-description {
            font-size: 18px;
            color: #768392;
            margin-bottom: 40px;
          }
        }
        .account-success-box {
          flex-direction: column;
          border-top: 3px solid #3963EF;
          .success-title {
            margin-top: 40px;
            margin-bottom: 12px;
            font-size: 32px;
            font-weight: 500;
            line-height: 33px;
            color: #383E44;
          }
          .success-description {
            font-size: 18px;
            color: #768392;
            margin-bottom: 40px;
          }
          .account-info-box {
            border-top: 1px solid #E6EBF0;
            padding: 32px 40px 40px 40px;
            display: flex;
            flex-direction: column;
            align-items: center;
            width: 100%;
            .btn-box {
              position: relative;
              margin-top: 40px;
              width: 100%;
              .steps-lines {
                width: 100%;
                display: flex;
                position: absolute;
              }
              .start-btn {
                height: 48px;
                width: 100%;
                border-color: #3963EF;
                background-color: #3963EF;
                span {
                  font-size: 18px;
                  color: #FFFFFF;
                }
              }
              .btn-disabled {
